body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #96deda;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white; /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #ffafbd,
    #c9ffbf
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

header {
  text-align: left;
  padding: 10px;
  background-color: #4a569d;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 11;
}

.app-body-container {
  width: 90%;
  margin-left: 5%;
  padding: 70px 0;
}

.left-panel {
  text-align: left;
  margin-bottom: 10px;
}

.lp-btn + .lp-btn {
  margin-left: 10px;
}

.autocomplete-input {
  width: 85%;
  display: inline-block;
}
.cross-icon {
  padding: 0 10px;
  color: black;
  font-weight: bold;
  font-size: 1.5rem;
  cursor: pointer;
}

.text-red {
  color: red;
}

#googleMap {
  width: 100%;
  height: 70vh;
}

footer {
  background-color: #5fc3e4;
  padding: 10px;
  margin-top: 30px;
  width: 100%;
  position: fixed;
  bottom: 0;
}

a#footer-link {
  color: orangered;
}

